  /* outline: 0px solid red; */
}
@font-face {
  font-family:'icons';
  src: url('../fonts/sap-icofonts.woff');
}

.accordion {
  width: 250px;
  margin: 20px;
  /* outline: 2px solid white; */
  
}
label {
  display: block;
  }

input {
  display: none;
  }

.accordion_item {
  display: block;
  border: 1px solid black;
  padding: 10px 30px;
  margin: 0;
  cursor: pointer;
  position: relative;
  }
  .accordion_item:after {
    content: '\E00D';
    display: block;
    font-family: 'icons';
    position: absolute;
    top:  50%;
    right: 20px;
    transform: translateY(-50%);
  }

  .input:checked ~ .accordion_item:after {
    content: '\E00F';
  }

.accordion_deskr {
  max-height: 0;
  overflow: hidden;
  transition: max-height .5s ease;
  border: 1px solid gray;
  border-top: none;
     }
  input:checked ~ .accordion_deskr {
      max-height: 100vh;
      transition: max-height .5s ease;
  }